.projects_main_container {
  width: 100%;
  /* border: 1px solid rgba(255, 0, 0, 0); */
  margin-top: 10rem;
}
.projects_heading {
  width: 90%;
  max-width: 1247px;
  margin: auto;
  /* max-width: 1247px; */
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
  color: #00ff00;
  margin-bottom: 3rem;
}

.projects_main {
  width: 90%;
  margin: auto;
  max-width: 1247px;
  /* border: 1px solid cyan; */
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  gap: 40px;
}
.project_card_main {
  padding: 1.5rem 0;
  border-radius: 10px;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  background-color: #212237;
  /* border: 2px solid #00ff00; */
  box-shadow: rgba(0.2, 0.2, 0.2, 0.4) 2px 2px 4px,
    rgba(0.2, 0.2, 0.2, 0.3) 0px 7px 13px -3px,
    rgba(0.1, 0.1, 0.1, 0.2) 0px -3px 0px inset;
  border-right: 4px solid #00ff00;
}
.project_number_a {
  color: #00ff00;
  font-size: 3rem;
}
.project_card_div_a {
  width: 49%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.project_card_div_a > img {
  width: 80%;
  border-radius: 10px;
  background-color: #00ff00;
  filter: brightness(90%);
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.project_card_div_b {
  width: 49%;
}
.project_card_div_b > h4 {
  color: #00ff00;
  /* border: 1px solid red; */
}
.project_card_div_b > p {
  color: rgb(255, 255, 255);
  text-align: justify;
  font-weight: 0;
}
.all_tech_stack_used {
  color: #ffffff;
  display: flex;
  font-size: 1.5rem;
  gap: 10px;
}
.buttons_container {
  /* border: 1px solid red; */
  width: 100%;
  display: flex;
  gap: 15px;
  margin-top: 1rem;
}
.buttons_container > button {
  height: 30px;
  width: 110px;
  display: flex;
  border-radius: 3px;
  align-items: center;
  justify-content: space-around;
  font-size: 13px;
  border: 2px solid #00ff00;
  background-color: rgba(0, 0, 0, 0);
  color: #00ff00;
  cursor: pointer;
  transition: 0.4s ease-out;
}

.buttons_container > button:hover {
  background-color: #00ff00;
  color: #000000;
  border: 2px solid #00ff00;
  padding: 0 2.2rem;
  transition: 0.3s ease-out;
}
/* MEDIA QUERY */
@media screen and (min-width: 1024px) {
  .project_number_b {
    display: none;
  }
  .project_card_div_b > h4 {
    margin-top: -1rem;
  }
  .project_number_a {
    margin-top: -1rem;
  }
}
@media screen and (max-width: 1024px) and (min-width: 481px) {
  .project_card_main {
    flex-direction: column;

    border-right: none;
    border-bottom: 4px solid #00ff00;
  }
  .project_card_div_a {
    width: 90%;
    margin-bottom: 1rem;
  }
  .project_card_div_a > img {
    width: 100%;
  }
  .project_card_div_b {
    width: 90%;
  }
  .project_number_a {
    display: none;
  }
  .project_number_b {
    color: #00ff00;
    font-size: 3rem;
    align-self: flex-start;
    width: 90%;
    margin: auto;
    margin-bottom: 1rem;
  }
}
@media screen and (max-width: 481px) {
  .project_card_main {
    flex-direction: column;
    border-right: 4px solid #00ff0000;
    border-bottom: 4px solid #00ff00;
  }
  .project_card_div_a {
    width: 90%;
    margin-bottom: 1rem;
  }
  .project_card_div_a > img {
    width: 100%;
  }
  .project_card_div_b {
    width: 90%;
  }
  .project_number_a {
    display: none;
  }
  .project_number_b {
    color: #00ff00;
    font-size: 3rem;
    align-self: flex-start;
    width: 90%;
    margin: auto;
    margin-bottom: 1rem;
  }
  .project_card_div_b > h1 {
    border: 1px solid red;
  }
  .buttons_container {
    justify-content: space-between;
  }
  .projects_heading {
    margin-bottom: 1.5rem;
  }
}
