.git_stats_main_container {
  margin-top: 15rem;
  /* border: 1px solid red; */
}

.git_calender {
  width: 90%;
  max-width: 1247px;
  margin: auto;
  display: flex;
  justify-content: center;
  color: #00ff00;
  margin-bottom: 5rem;
}
.git_heading {
  color: #00ff00;
  width: 90%;
  max-width: 1247px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4rem;
}
.git_stats_streaks {
  width: 90%;
  margin: auto;
  max-width: 1247px;
  display: flex;
  gap: 20px;
  margin-bottom: 15rem;
  /* border: 1px solid red; */
}
.git_stats_streaks > img {
  width: 50%;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
/* MEDIA QUERY */
@media screen and (max-width: 1024px) and (min-width: 481px) {
  .git_stats_streaks {
    flex-direction: column;
  }
  .git_stats_streaks > img {
    width: 100%;
  }
}
@media screen and (max-width: 481px) {
  .git_calender {
    margin-bottom: 2rem;
  }
  .git_stats_streaks {
    flex-direction: column;
  }
  .git_stats_streaks > img {
    width: 100%;
  }
  .git_heading {
    margin-bottom: 2rem;
  }
}
